import { mapGetters, mapMutations, mapActions } from 'vuex'
import { ProjectInfo } from 'api/index.js'
import { getStoreUserSession } from 'api/UserSession.js'
import { formatDate, clearStorage } from 'common/js/Util.js'

export default {
  props: {
    type: {
      type: String,
      default: 'project'
    }
  },
  data () {
    return {
      projectInfoMsg: {},
      UserSession: {}
    }
  },
  computed: {
    ...mapGetters([
      'projectInfo'
    ])
  },
  created () {
    this.UserSession = getStoreUserSession()
  },
  mounted () {
    if (this.type === 'project') {
      if (this.timer) {
        clearTimeout(this.timer)
      }

      this.timer = setTimeout(() => {
        this.getProjectInfo()
      }, 500)
    }
  },
  methods: {
    // 获取项目详情
    getProjectInfo () {
      let { EpsProjId } = this.UserSession
      ProjectInfo(EpsProjId).then((response) => {
        this.setProjectInfo(response.data)
        this.projectInfoMsg = Object.assign({}, response.data)
      }).catch((e) => {
        console.log(e.message)
        if (e.message === '201') {
          clearStorage()
          this.$router.push('/login')
        }
        // this.AlertShowEvent(e.message)
      })
    },
    _formatDate (time) {
      return formatDate(time)
    },
    // 打开切换项目面板
    changeShow () {
      this.$router.push('/changeproject')
    },
    ...mapMutations({
      setProjectInfo: 'SET_PROJECTINFO'
    }),
    ...mapActions([
      'AlertShowEvent'
    ])
  }
}



